import styles from './header.module.scss';

const headerItems = {
  home: 'главная',
  about: 'о нас',
  contacts: 'контакты',
  vacancies: 'вакансии'
};

export default function Header() {
  return (
    <header className={styles.header}>
      {
        Object.entries(headerItems).map(([key, value]) => 
          <button className={styles.item} key={key}>
            {value.toUpperCase()}
          </button>
        )
      }
    </header>
  );
}
